@use 'base/variables' as v;
@use 'base/mixins' as m;

.references {
    margin-top: 15rem;
    &__text {
        text-align: center;

        @include m.tablet { 
            text-align: left;
        }
    }

    &__subtitle {
        color: v.$primaryColor;
        font-weight: 700;
    }

    &__paragraph {
        display: grid;
        margin-bottom: 5rem;
        line-height: 1.5;
    }    
}