@use 'base/variables' as v;
@use 'base/mixins' as m;

.team {
    margin-bottom: 20rem;
    &__grid {
        //height: 30rem;
        @include m.tablet { 
            @include m.grid(4, 2rem);
            gap: 6rem;
        }
    }

    &__card {
        box-shadow: 0px 13px 19px 0px rgb(0 0 0 / 0.2);
        border-radius: 3rem 3rem 2rem 2rem;

        margin-bottom: v.$gap * 2;

        @include m.tablet { 
            margin-bottom: 0;
        }
    }

    &__image {
        height: 50rem;
        object-fit: cover;
        width: 100%;
        border-radius: 3rem 3rem 0 0;

        @include m.tablet { 
            height: 25rem;
        }
    }

    &__content {
       display: flex;
       flex-direction: column;
       align-items: center;
       text-align: center;
       padding: 3rem 0;
    }

    &__doctorname {
        color: v.$textPrimaryColor;
        font-weight: 700;
        font-size: 1.5rem;
        margin: 0;
    }

    &__profession {
        font-size: 1rem;
        font-weight: 700;

        @include m.desktop { 
            font-size: 1.2rem;
        }
    }

    &__social {
        display: flex;
        justify-content: center;
        gap: 2rem;
    }

    &__social-icon {
        width: 2rem;
    }
}