@use 'base/variables' as v;
@use 'base/mixins' as m;

.brands {
    
    margin-top: 15rem;
    margin-bottom: 15rem;

    &__grid {
        @include m.tablet { 
            @include m.grid(6, 2rem);
        }
    }

    &__image {
        max-width: 20rem;
        margin: 0 auto 6rem auto;
        @include m.tablet {
            display: flex;
            margin: unset;
        }
    }
}