@use 'variables' as v; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$font;
}

p {
    color: v.$textSecondaryColor;
    font-size: 1.4rem;
}

input {
    border: none;
}

.container {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

a {
    font-size: 1.4rem;
    text-decoration: none;
    color: v.$white;
}

img {
    max-width: 100%;
    display: block;
}

h1, h2, h3 {
    margin: 0 0 v.$gap 0;
    color: v.$textPrimaryColor;
}

h1 {
    font-size: 5.8rem;
}

h2 {
    font-size: 4rem;
}

h3 {
    font-size: 2.4rem;
}