@use 'variables' as v; 

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin bigScreen {
    @media (min-width: v.$bigScreen) {
        @content;
    }
}

// Buttons
@mixin button($fillColor, $textColor, $isBackground: true) {
    
    display: block;
    background-color: transparent;
    text-transform: capitalize;
    padding: 1.5rem 2rem;
    text-align: center;
    transition-property: color, background-color;
    transition-duration: 150ms;
    color: $textColor;

    @include tablet { 
        display: inline-block;
    }

    &:hover {
        color: $fillColor;
        background-color: transparent;
    }
    
    @if $isBackground {
        background-color: $fillColor;
        font-weight: 700;
        text-transform: uppercase;
        border-radius: 0.4rem;

        &:hover {
            color: v.$white;
            background-color: darken($fillColor, 5%);
        }
    }
}

@mixin grid($columnas, $space) {
    display: grid;
    gap: $space;
    grid-template-columns: repeat($columnas, 1fr);
}