@use 'base/variables' as v;
@use 'base/mixins' as m;

.newsletter {

    &__subtitle,
    &__heading,
    &__paragraph {
        text-align: center;
    }

    &__subtitle {
        font-size: 1.2rem;
        font-weight: 700;
        color: v.$primaryColor;
    }

    &__heading {
        text-transform: uppercase;
        margin-bottom: 1.5rem;
    }

    &__paragraph {
        font-size: 1.3rem;
        margin: 0 auto;
        

        @include m.tablet { 
            width: 50%;
        }
    }

    &__content {
        margin: 8rem auto 0 auto;
        width: 100%;

        @include m.tablet { 
            width: 50%;
        }
    }

    &__form {
        @include m.tablet { 
            display: flex;
            align-items: center;
        }
    }

    &__field {
        flex: 1;
    }

    &__label {
        display: none;
    }

    &__input {
        border: 2px solid #E6E6E6;
        width: 100%;
        padding: 1.1rem 2rem;
        font-size: 1.4rem;
        color: v.$textSecondaryColor;
        border-radius: 0.5rem;
        background-color: #F9F9F9;

        @include m.tablet { 
            border-right: 0;
            border-radius: 0;
            border-top-left-radius: 0.5rem;
            border-bottom-left-radius: 0.5rem;
        }
    }

    &__submit {
        @include m.button(v.$primaryColor, v.$white);
        flex: 0.2;
        width: 100%;
        margin-top: 2rem;

        @include m.tablet { 
            margin-top: 0;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
        &:hover {
            cursor: pointer;
        }
    }
}