@use 'base/variables' as v;
@use 'base/mixins' as m;

.navbar {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet { 
        flex-direction: row;
    }

    &__link {
        margin-bottom: 2rem;
        font-weight: 700;
        transition-property: color;
        transition-duration: 150ms;

        @include m.tablet { 
            margin-bottom: 0;
            margin-right: 2rem;
        }

        &:last-of-type {
            margin-bottom: 0;
        }

        &:hover {
            color: v.$primaryColor;
        }
    }
}