@use 'base/variables' as v;

.text-center {
    text-align: center;
}
.color-text-white {
    color: v.$white;
}
.color-border-white {
    color: v.$white;
    &::after {
        content: "";
        display: block;
        margin-top: 2rem;
        border-bottom: 2px solid v.$white;
        width: 5rem;
    }
}
.line-height-15 {
    line-height: 1.5;
}
.img-cover {
    height: 100%;
    object-fit: cover;
}
