@use 'base/variables' as v;
@use 'base/mixins' as m;

.hero {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 20rem;
    height: 100%;

    &__paragraph {
        padding-top: 8rem;
        color: v.$primaryColor;
        font-weight: 700;
        margin-bottom: v.$gap;
    }

    &__heading {
        color: v.$white;
        margin: 0 auto;
        width: 80%;
        @include m.tablet { 
            font-size: 5.8rem;
            width: 70%;
        }
        @include m.desktop { 
            width: 40%;
        }
    }

    &__phrase {
        margin-top: v.$gap;
        color: v.$white;
        font-size: 2rem;
        font-weight: 200;
    }

    &__links {
        margin-top: v.$gap;
    }

    &__link {
        @include m.button(v.$primaryColor, v.$white);

        &--unbackground {
            @include m.button(v.$primaryColor, v.$primaryColor, false);
            border: 1px solid v.$primaryColor;
            margin-top: 2rem;
            
            @include m.tablet { 
                margin: 0 0 0 2rem;
            }
            padding: 1.5rem v.$gap;
        }
    }
}