@use 'base/variables' as v;
@use 'base/mixins' as m;

.card {

    &__grid {
        display: grid;
        grid-template-rows: repeat(4, 1fr);
        gap: v.$gap;
        
        @include m.desktop { 
            grid-template-rows: unset;
            grid-template-columns: repeat(4, 1fr);
            height: 40rem;
            grid-auto-rows: min-content;
        }
    }

    &__content {
        box-shadow: 0px 13px 19px 0px rgb(0 0 0 / 0.2);
        padding: v.$gap;
        width: 90%;
        margin: 0 auto;

        &:nth-child(3n) {
            background-color: v.$primaryColor;
        }


        @include m.desktop { 
            &:nth-child(even) {
                transform: translateY(5rem);
            }    
        }
    
    }

    &__image {
        margin-bottom: 2rem;
    }

    &__heading {
        font-size: 1.5rem;

        &::after {
            content: "";
            display: block;
            margin-top: 2rem;
            border-bottom: 2px solid v.$primaryColor;
            width: 5rem;
        }
    }

    &__paragraph {
        line-height: 1.5;

        @include m.tablet { 
            width: 80%;
        }
    }
}