@use 'base/variables' as v;
@use 'base/mixins' as m;

.activities {

    &__grid {
        @include m.tablet { 
            @include m.grid(4, 1fr);
            column-gap: 0.2rem;    
        }
        @include m.desktop { 
            column-gap: 1rem;    
        }
    }
    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2.5rem;

        @include m.tablet { 
            align-items: unset;
        }
    }

    &__image {
        position: relative;
        max-width: 28rem;
        height: 42rem;
        margin: 0 auto;
        &::before {
            content: "Sale";
            position: absolute;
            top: 2rem;
            left: 2rem;
            color: v.$white;
            font-weight: 700;
            font-size: 1.5rem;
            border-radius: 0.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 7rem;
            height: 3rem;
            background-color: v.$red;
        }
    }

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
    }

    &__subtitle {
        color: v.$primaryColor;
        font-weight: 700;
        margin-right: 2rem;
    }

    &__stars {
        background-color: v.$secondaryColor;
        display: flex;
        flex-direction: row-reverse;
        padding: 0 .8rem;
        border-radius: 4rem;
        align-items: center;
        gap: 0.5rem;
        height: 3rem;
    }
    &__grade {
        margin: 0;
        color: v.$white;
    }

    &__heading {
        font-size: 2rem;
        margin-bottom: 1rem;
        text-align: center;
        @include m.tablet { 
            text-align: left;
        }
    }
    &__paragraph {
        text-align: center;
        @include m.tablet { 
            text-align: left;
        }
    }
    &__icons,
    &__prices {
        display: flex;
        align-items: center;
        gap: 1rem;
        font-weight: 700;
        margin: 1rem 0;
    }

    &__price {
        font-size: 1.8rem;
        margin-top: 1rem;
        color: v.$gray;

        &--blue {
            color: v.$secondaryColor;
        }
    }

    &__button {
        @include m.button(v.$primaryColor, v.$primaryColor, false);
        border: 1px solid v.$primaryColor;
        font-size: 1.2rem;
        border-radius: 5rem;
        font-weight: 700;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        @include m.desktop { 
            width: fit-content;
        }
    }

}