@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    padding: v.$gap 0;
    background-color: v.$tertiaryColor;
    position: relative;
    overflow: hidden;

    &::after {
        content: "";
        background-color: v.$white;
        width: 130%;
        height: 15%;
        position: absolute;
        bottom: -5rem;
        left: -3rem;
        transform: rotate(-3deg);
    }

    @include m.bigScreen { 
        &::after {
            transform: rotate(-2deg);
        }
    }

    &__menu {
        display: flex;
        flex-direction: column;
        @include m.tablet { 
            display: flex;
            flex-direction: row;
            justify-content: space-between;    
        }
    }

    &__bar {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet { 
            flex-direction: row;
        }
    }

    &__heading {
        font-size: 2.4rem;
        color: v.$white;

        @include m.tablet { 
            margin-right: v.$gap * 2;
            margin-bottom: 0;
        }
    }

    &__buttons {
        margin-top: v.$gap;
        @include m.tablet { 
            margin-top: 0;
            display: flex;
        }
    }

    &__button {
        @include m.button(v.$primaryColor, v.$white);

        &--unbackground {
            @include m.button(v.$primaryColor, v.$white, false);
        }

        &--icon {
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0;
            color: v.$white;
            &::after {
                content: "";
                display: block;
                flex-direction: column;
                align-items: center;
                background-image: url(../img/icon_arrow.svg);
                background-repeat: no-repeat;
                height: 1.4rem;
                width: 1.4rem;
                margin-left: 0.5rem;
            }
        }
    }
}