@use 'base/variables' as v;
@use 'base/mixins' as m;

.statistics {
    text-align: center;
    margin-top: v.$gap * 3;

    &__grid {
        display: grid;
        grid-template-rows: repeat(4, 1fr);
        row-gap: v.$gap;

        @include m.tablet { 
            grid-template-rows: unset;
            grid-template-columns: repeat(4, 1fr);
        }
    }

    &__heading {
        font-size: 5rem;
        color: v.$primaryColor;
        text-transform: uppercase;
    }

    &__paragraph {
        color: v.$black;
        font-weight: 700;
    }
}