@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$secondaryColor;
    padding: 5rem 0;
    margin-top: 8rem;

    @include m.tablet { 
        padding: 7rem;
    }

    &__grid {
        display: flex;
        flex-direction: column;

        @include m.tablet { 
            flex-direction: row;
        }
    }

    &__links {
        display: flex;
        flex-direction: column;
        align-items: center;
        
        @include m.tablet { 
            margin-right: 10rem;
            align-items: flex-start;
        }
        
        &:last-of-type {
            margin-right: 0;
        }
    }

    &__link {
        &--flex {
            display: flex;
            gap: 2rem;
            margin-bottom: 1rem;
        }
    }

    &__link, &__ref {
        font-weight: 700;
        line-height: 2;
        font-size: 1.2rem;
        transition: color 150ms;

        &:nth-child(1) {
            font-size: 1.7rem;
            margin: 2rem 0;
            color: v.$primaryColor;
        }

        @include m.tablet { 
            &:nth-child(1) { 
                color: v.$white;
            } 
        }

        &:hover {
            color: v.$primaryColor;
        }
    }

    &__icon {
        width: 1.6rem;
    }
}