@use 'base/variables' as v;
@use 'base/mixins' as m;

.doctors {
    @include m.tablet { 
        margin-top: v.$gap;
    }

    &--blue {
        background-color: v.$secondaryColor;
    }

    &__grid {
        display: flex;
        flex-direction: column-reverse;
        padding: 10rem 0 2rem 0;
        
        @include m.tablet { 
            @include m.grid(2, 5rem);
            align-items: center;
        }
    }

    &__graphic {
        width: 90%;
        margin: 0 auto;
        position: relative;

        &::before {
            content: "";
            background: linear-gradient(to top, v.$black -30%, transparent 100%);
            position: absolute;
            border-radius: 3rem;
            width: 100%;
            height: 100%;
        }

        &::after {
            content: "▶";
            color: v.$white;
            font-size: 2rem;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: v.$primaryColor;
            position: absolute;
            width: 7rem;
            height: 7rem;
            border-radius: 50%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        &:hover {
            cursor: pointer;
        }
    }

    &__video {
        border-radius: 3rem;
    }

    &__content {
        text-align: center;
        line-height: 1.8;
        @include m.tablet { 
            text-align: left;
            max-width: 70%;
        }
    }

    &__heading {
        font-size: 4rem;
        line-height: 1.5;

        @include m.tablet { 
            &::before {
                content: "";
                border-top: 5px solid v.$primaryColor;
                display: block;
                width: 30%;
                margin-bottom: 2rem;
            }    
        }

        &--white {
            color: v.$white;
        }
    }

    &__paragraph {
        &--white {
            color: v.$white;
        }
    }

    &__button {
        @include m.button(v.$primaryColor, v.$primaryColor, false);
        padding: 0;
        margin: 2rem 0;
        font-weight: 700;

        @include m.tablet { 
            margin-bottom: 0;
        }

        &::after {
            content: ">";
            margin-left: 1rem;
        }
    }
}